@use "sass:color";
@use "../config" as *;

/***************
FORM STYLING
***************/

label {
	display: block;
	margin-bottom: 8px;
	font-size: 1.1em;
	font-weight: 500;
}
input[type="text"],
input[type="email"],
input[type="phone"],
input[type="password"],
input[type="number"],
input[type="search"],
textarea {
	background: color.adjust(white, $lightness: -10%);
	padding: 12px 16px;
	outline: none;
	border: 2px solid $silver;
	border-radius: 6px;
	font-size: 1.1em;
	font-family: inherit;
	height: auto;
	min-height: 48px;
	width: 300px;
	margin-bottom: 0.5rem;
	transition: border-color 0.2s ease;

	&:focus {
		border-color: $primary-color;
		box-shadow: 0 0 0 3px color.adjust($primary-color, $alpha: -0.8);
	}

	&.full-width {
		width: 100%;
	}
}
textarea {
	min-height: 80px;
	resize: vertical;
}

form {
	margin-bottom: $padding;

	// Wrap each form input/element in a field div
	.field {
		margin-bottom: $padding;
	}

	// Notes go under the input fields
	.note {
		margin-top: 5px;
		color: $subtle-color;
	}

	// Required label
	.req {
		font-style: italic;
	}

	// Validation, add error class to the item div
	.error {
		// Validation reason
		.reason {
			margin-top: 5px;
			color: $bad-color;
		}

		input[type="text"],
		input[type="email"],
		input[type="phone"],
		input[type="password"],
		input[type="number"],
		input[type="search"],
		textarea {
			border-color: $bad-color;
		}
	}
}

@media (prefers-color-scheme: dark) {
	label {
		color: $white;
	}
	
	input[type="text"],
	input[type="email"],
	input[type="phone"],
	input[type="password"],
	input[type="number"],
	input[type="search"],
	textarea {
		background: color.adjust($dark-bg, $lightness: 8%);
		color: $white;
		border-color: $grey;
		
		&::placeholder {
			color: $silver;
		}
		
		&:focus {
			border-color: $primary-color;
			box-shadow: 0 0 0 3px color.adjust($primary-color, $alpha: -0.8);
		}
	}
	
	form .note {
		color: $silver;
	}
}
