@use "../config" as *;

// Comments styling for light and dark modes

.comments {
  margin-top: 3rem;
  padding-top: 2rem;
  border-top: 2px solid #e6e6e6;

  h3 {
    margin-bottom: 1.5rem;
    color: #333;
    font-family: $sans-serif;
    font-weight: 600;
  }
}

.comment-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.comment {
  background: #f8f9fa;
  border: 1px solid #e6e6e6;
  border-radius: 8px;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
  position: relative;

  .comment-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
    padding-bottom: 0.5rem;
    border-bottom: 1px solid #e0e0e0;

    strong {
      font-size: 1.1em;
      color: #5a7400;
      font-family: $sans-serif;

      a {
        color: inherit;
        text-decoration: none;

        &:hover {
          text-decoration: underline;
        }
      }
    }

    time {
      font-size: 0.9em;
      color: #666;
      font-family: $sans-serif;
      font-weight: 300;
    }
  }

  .comment-body {
    color: #333;
    line-height: 1.6;

    p {
      margin-bottom: 0.5rem;
      width: 100%;

      &:last-child {
        margin-bottom: 0;
      }
    }

    // Style any markdown content in comments
    blockquote {
      border-left: 4px solid #5a7400;
      margin: 1rem 0;
      padding-left: 1rem;
      color: #666;
      font-style: italic;
    }

    code {
      background: #f0f0f0;
      padding: 0.2em 0.4em;
      border-radius: 3px;
      font-size: 0.9em;
    }

    pre {
      background: #f0f0f0;
      padding: 1rem;
      border-radius: 4px;
      overflow-x: auto;
    }
  }

  .comment-actions {
    margin-top: 1rem;
    font-size: 0.9rem;

    .comment-reply-link {
      color: #5a7400;
      text-decoration: none;
      font-weight: 600;

      &:hover {
        text-decoration: underline;
      }
    }
  }
}

// Nested comment replies
.comment-replies {
  list-style: none;
  padding: 0;
  margin: 1.5rem 0 0 2rem;

  .comment {
    background: #fff;
    border-left: 3px solid #5a7400;
    margin-bottom: 1rem;
  }
}

// Comment form styling
.comment-form {
  margin: 0 auto 2rem;
  margin-top: 3rem;
  padding: 2rem;
  background: #f8f9fa;
  border-radius: 8px;
  border: 1px solid #e6e6e6;
  width: 75%;

  h3 {
    margin-bottom: 1rem;
    color: #333;
    font-family: $sans-serif;
  }

  p {
    margin-bottom: 1.5rem;
    color: #666;
    font-style: italic;
  }

  .comment-form-fields {
    display: grid;
    gap: 1rem;
  }

  #reply-to-display {
    background: #e9ecef !important;
    border-left: 4px solid #5a7400;
    color: #333;

    strong {
      color: #5a7400;
    }

    #cancel-reply {
      font-weight: bold;

      &:hover {
        color: #333 !important;
      }
    }
  }

  label {
    font-weight: 600;
    color: #333;
  }

  input[type="text"],
  input[type="email"],
  input[type="url"],
  textarea {
    width: 100%;
    padding: 0.75rem;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-family: inherit;
    font-size: 1rem;
    transition: border-color 0.2s ease;

    &:focus {
      border-color: #5a7400;
      outline: none;
      box-shadow: 0 0 0 2px rgba(90, 116, 0, 0.2);
    }
  }

  textarea {
    min-height: 120px;
    resize: vertical;
  }
}

// Comment submission messages
.comment-message {
  margin: 1rem 0;

  div {
    padding: 1rem;
    border-radius: 4px;
    text-align: center;

    h4 {
      margin-top: 0;
      margin-bottom: 0.5rem;
    }

    p {
      margin-bottom: 0;
    }
  }

  &.comment-message-success div {
    background: #d4edda;
    border: 1px solid #c3e6cb;
    color: #155724;
  }

  &.comment-message-error div {
    background: #f8d7da;
    border: 1px solid #f5c6cb;
    color: #721c24;
  }
}

// Dark mode styles
@media (prefers-color-scheme: dark) {
  .comments {
    border-top-color: #333;

    h3 {
      color: #fff;
    }
  }

  .comment {
    background: #1e1e1e;
    border-color: #333;

    .comment-header {
      border-bottom-color: #333;

      strong {
        color: #95c100;
      }

      time {
        color: #ccc;
      }
    }

    .comment-body {
      color: #e0e0e0;

      blockquote {
        border-left-color: #95c100;
        color: #ccc;
      }

      code {
        background: #2a2d33;
        color: #e0e0e0;
      }

      pre {
        background: #2a2d33;
        color: #e0e0e0;
      }
    }

    .comment-actions {
      .comment-reply-link {
        color: #95c100;
      }
    }
  }

  .comment-replies {
    .comment {
      background: #2a2d33;
      border-left-color: #95c100;
    }
  }

  .comment-form {
    background: #1e1e1e;
    border-color: #333;

    h3 {
      color: #fff;
    }

    p {
      color: #ccc;
    }

    label {
      color: #fff;
    }

    #reply-to-display {
      background: #2a2d33 !important;
      color: #fff;
      border-left-color: #95c100;

      strong {
        color: #95c100;
      }

      #cancel-reply {
        color: #888;

        &:hover {
          color: #fff !important;
        }
      }
    }

    input[type="text"],
    input[type="email"],
    input[type="url"],
    textarea {
      background: #2a2d33;
      border-color: #444;
      color: #fff;

      &::placeholder {
        color: #888;
      }

      &:focus {
        border-color: #95c100;
        box-shadow: 0 0 0 2px rgba(149, 193, 0, 0.2);
      }
    }
  }

  .comment-message {
    &.comment-message-success div {
      background: #155724;
      border-color: #0a4622;
      color: #d4edda;
    }

    &.comment-message-error div {
      background: #721c24;
      border-color: #5a1a1a;
      color: #f8d7da;
    }
  }
}

// Responsive adjustments
@media only screen and (min-width: 800px) {

  .comments,
  .comment-form {}

  .comment-list {
    width: 100%;
  }

  .comment {
    width: 100%;
  }

  .comment-replies {
    margin-left: 3rem;
  }
}